$template-name: "t-sidebar";

.#{$template-name} {
  width: 300px;
  display: flex;
  flex-flow: column;
  @include breakpoint(medium-only) {
    width: auto;
  }
  span {
    text-transform: uppercase;
    margin-block-end: var(--padding);
    font-size: 14px;
    letter-spacing: 0.2em;
  }
  &--right {
    margin-inline-start: 50px;
    @include breakpoint(medium-only) {
      margin-inline-start: 0;
    }
  }
  &--left {
    margin-inline-end: 50px;
    @include breakpoint(medium-only) {
      margin-inline-end: 0;
    }
  }
}
